@mixin phone
  @media (max-width: 600px)
    @content
@mixin tab-port
  @media (max-width: 900px)
    @content
@mixin tab-land
  @media (max-width: 1240px)
    @content
@mixin big-screen
  @media (min-width: 1800px)
    @content

@mixin respond($breakpoint)
  @if $breakpoint == phone
    @media only screen and (max-width: 600px)
      @content
  @if $breakpoint == tab-port
    @media only screen and  (max-width: 900px)
      @content
  @if $breakpoint == tab-land
    @media only screen and  (max-width: 1240px)
      @content
  @if $breakpoint == big-screen
    @media only screen and  (min-width: 1800px)
      @content

*, ::after, ::before
  margin: 0
  padding: 0
  box-sizing: border-box

html
  font-size: calc( 10 / 16 * 100%)
  color: white
  font-family: Arial, sans-serif
  @include respond(tab-land)
    font-size: 56.25%
  @include respond(tab-port)
    font-size: 50%
  @include respond(phone)
    font-size: 40%
  @include respond(big-screen)
    font-size: 75%

.page
  height: 85vh
  padding: 2rem
  background-size: cover
  background: url("../../src/images/bg-weather.jpg") no-repeat fixed

.header
  display: flex
  justify-content: space-between
  margin-bottom: 10rem
  &__form
    display: flex
    gap: 1rem
    align-items: center
    &-btn
      background-color: transparent
      border: none
      cursor: pointer

.header__logo-image
  animation: pulse 2s infinite

#header__form-input
  font-size: 2rem
  background-color: transparent
  border: none
  border-bottom: 3px solid white
  color: white
  padding-bottom: 6px
  &::placeholder
    color: white
    font-size: 2rem

#header__form-input:focus
  animation: grow 0.3s forwards

.description
  font-weight: normal
  font-size: 2rem

.animate
  animation: fadeIn 2s

.weather
  display: flex
  height: 100%
  gap: 10rem
  flex-wrap: wrap
  justify-content: center
  &__details
    width: 50%
    padding: 5rem
    background: rgba(0, 0, 0, 0.27)
    border-radius: 1.6rem
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1)
    backdrop-filter: blur(7.1px)
    -webkit-backdrop-filter: blur(7.1px)
    border: 1px solid rgba(0, 0, 0, 0.3)
    display: flex
    flex-direction: column
    gap: 6.5rem
    @include respond(tab-port)
      width: 80%
    @include respond(phone)
      width: 100%
  &__detail
    &-container
      display: flex
      flex-direction: column
      gap: 3.5rem
    &-block
      display: flex
      justify-content: space-between
      &-values
        display: flex
        gap: 1rem
    &-condition
      font-weight: bold
  &__display
    display: flex
    align-items: center
    gap: 3rem
    &-degrees
      font-size: 14rem
    &-header
      font-size: 6rem
    &-description
      font-size: 1.8rem
    &-img
      @include respond(phone)
        display: none

@keyframes pulse
  0%
    transform: scale(1)
  50%
    transform: scale(1.1)
  100%
    transform: scale(1)

@keyframes fadeIn
  0%
    opacity: 0
  100%
    opacity: 1

@keyframes grow
  0%
    transform: scale(1)
  100%
    transform: scale(1.05)
